body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    /* font-family: "Lucida Console", Monaco, monospace; */
    
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 1250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

.loginForm {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #ffa500;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
  font-size: 14px;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: orange;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #f8f8f8; 
    cursor: pointer;
}

.content {
    margin-top: 100px;
    margin-left: 150px;
    margin-right: 150px;
}

.confirmation {
    text-align: center;
    margin-top: 150px;
}

.availableProjectsHeading {
    font-size: 20px;
}

.popup {
    text-align: center;
}